import NavBar from '#app/layouts/navbar/navbar.vue';
import { get as getConfig, hasPermission } from '#helpers/api/config';
import Form from '#app/components/form/form.vue';
import { add } from '#helpers/api/town';

export default {
    components: {
        NavBar,
        Form,
    },
    data() {
        const {
            field_types: fieldTypes,
            owner_types: ownerTypes,
            social_origins: socialOrigins,
            electricity_types: electricityTypes,
        } = getConfig();

        const censusStatuses = [
            { value: 'none', label: 'Non prvu' },
            { value: 'scheduled', label: 'Prvu' },
            { value: 'done', label: 'Ralis' },
            { value: null, label: 'Inconnu' },
        ];

        const yesNoValues = [
            { value: 1, label: 'Oui' },
            { value: 0, label: 'Non' },
            { value: -1, label: 'Inconnu' },
        ];

        const cfpValues = [
            { value: 'none', label: 'Non demand' },
            { value: 'requested', label: 'Demand' },
            { value: 'granted', label: 'Obtenu' },
            { value: null, label: 'Inconnu' },
        ];

        const formDefinition = {
            title: 'Dclaration d\'un site',
            description: 'Ce formulaire vous permet de dclarer un site identifi sur votre territoire (bidonville ou squat). Il est divis en quatre onglets : caractristiques, dmographie, conditions de vie et procdure judiciaire d\'expulsion des occupants. Lorsque le symbole (*) apparat en rouge au dbut de l\'intitul de la question, cela signifie qu\'il s\'agit d\'une question obligatoire. Il n\'est pas possible de valider le formulaire sans y avoir rpondu.',
            steps: [
                {
                    title: 'Caractristiques du site',
                    sections: [
                        ...[
                            {
                                title: 'Caractristiques',
                                inputs: {
                                    address: {
                                        type: 'addressWithLocation',
                                        label: 'Localisation gographique',
                                        description: 'Saisissez ici l\'adresse du site, puis prcisez sa position en dplaant le point sur la carte.',
                                        mandatory: true,
                                    },
                                    name: {
                                        type: 'text',
                                        label: 'Appellation du site',
                                        description: 'Exemples : Entrept de la Poste, Rond-point du centre...',
                                        mandatory: false,
                                        specificProps: {
                                            maxlength: 35,
                                        },
                                    },
                                    addressDetails: {
                                        type: 'text',
                                        label: 'Informations d\'accs',
                                        description: 'Saisissez ici toutes les informations qui, en plus de l\'adresse, peuvent tre utiles pour l\'accs au site.',
                                        mandatory: false,
                                    },
                                    priority: {
                                        type: 'radio',
                                        options: [
                                            { label: '1', value: 1 },
                                            { label: '2', value: 2 },
                                            { label: '3', value: 3 },
                                        ],
                                        label: 'Niveau de priorit du site',
                                        description: '1 tant le niveau de priorit le plus haut.<br/>Le niveau de priorit est un indicateur rserv aux correspondants des services de ltat.',
                                        mandatory: false,
                                    },
                                    builtAt: {
                                        type: 'date',
                                        label: 'Date d\'installation du site',
                                        mandatory: true,
                                    },
                                    declaredAt: {
                                        type: 'date',
                                        label: 'Date de signalement du site',
                                        mandatory: false,
                                    },
                                    fieldType: {
                                        type: 'radio',
                                        options: fieldTypes.map(({ id, label }) => ({
                                            label,
                                            value: id,
                                        })),
                                        label: 'Type de site',
                                        mandatory: true,
                                    },
                                    ownerType: {
                                        type: 'radio',
                                        options: ownerTypes.map(({ id, label }) => ({
                                            label,
                                            value: id,
                                        })),
                                        label: 'Type de propritaire',
                                        mandatory: true,
                                    },
                                    owner: {
                                        type: 'text',
                                        label: 'Identit du propritaire',
                                        condition({ ownerType }) {
                                            return ownerType && ownerTypes.find(({ id }) => id === ownerType).label !== 'Inconnu';
                                        },
                                    },
                                },
                            },
                            {
                                title: 'Habitants',
                                inputs: {
                                    censusStatus: {
                                        type: 'radio',
                                        options: censusStatuses,
                                        label: 'Statut du diagnostic social',
                                        description: 'Un diagnostic social vise  identifier les situations et besoins des familles et personnes, de reprer le contexte territorial et les acteurs en prsence.',
                                        mandatory: true,
                                    },
                                    censusConductedAt: {
                                        type: 'date',
                                        label: 'Date du diagnostic',
                                        mandatory: true,
                                        condition({ censusStatus }) {
                                            return ['scheduled', 'done'].indexOf(censusStatus) !== -1;
                                        },
                                    },
                                    censusConductedBy: {
                                        type: 'text',
                                        label: 'Service ou oprateur en charge du diagnostic',
                                        mandatory: true,
                                        condition({ censusStatus }) {
                                            return ['scheduled', 'done'].indexOf(censusStatus) !== -1;
                                        },
                                    },
                                    populationTotal: {
                                        type: 'number',
                                        label: 'Nombre de personnes',
                                        description: 'Laissez ce champ vide si l\'information est inconnue',
                                        mandatory: false,
                                    },
                                    populationCouples: {
                                        type: 'number',
                                        label: 'Nombre de mnages',
                                        description: 'Laissez ce champ vide si l\'information est inconnue',
                                        mandatory: false,
                                    },
                                    populationMinors: {
                                        type: 'number',
                                        label: 'Nombre de mineurs',
                                        description: 'Laissez ce champ vide si l\'information est inconnue',
                                        mandatory: false,
                                    },
                                    socialOrigins: {
                                        type: 'checkbox',
                                        options: socialOrigins.map(({ id, label }) => ({
                                            label,
                                            value: id,
                                        })),
                                        label: 'Origines',
                                        description: 'Ne rien cocher si l\'information est inconnue',
                                        mandatory: false,
                                        condition({ populationTotal }) {
                                            return parseInt(populationTotal, 10) > 10;
                                        },
                                        inactiveMessage: 'Pour les sites de 10 personnes ou moins, l\'origine des habitants ne peut tre prcise conformment  la rglementation relative aux donnes  caractre personnel',
                                    },
                                },
                            },
                            {
                                title: 'Conditions de vie',
                                inputs: {
                                    electricityType: {
                                        type: 'radio',
                                        options: electricityTypes.map(({ id, label }) => ({
                                            label,
                                            value: id,
                                        })),
                                        label: 'Accs  l\'lectricit',
                                        mandatory: true,
                                    },
                                    electricityComments: {
                                        type: 'textarea',
                                        label: 'Modalits d\'accs',
                                        description: 'Exemples : groupe lectrogne, raccordement  une usine...',
                                        mandatory: false,
                                    },
                                    accessToWater: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Accs  l\'eau',
                                        mandatory: true,
                                    },
                                    waterComments: {
                                        type: 'textarea',
                                        label: 'Modalits d\'accs',
                                        description: 'Exemples: Citerne de 100 litres remplie par les pompiers tous les jours, 3 robinets raccords par la collectivit, borne incendie  200 mtres...',
                                        mandatory: false,
                                    },
                                    accessToSanitary: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Accs  des toilettes',
                                        mandatory: true,
                                    },
                                    sanitaryComments: {
                                        type: 'textarea',
                                        label: 'Modalits d\'accs',
                                        description: 'Exemples : 3 toilettes sches, toilettes raccords au rseau, un bloc sanitaire fourni par la ville...',
                                        mandatory: false,
                                    },
                                    trashEvacuation: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'vacuation des dchets',
                                        mandatory: true,
                                    },
                                },
                            },
                        ],
                        ...hasPermission('shantytown.create.data_justice')
                            ? [{
                                title: 'Procdure judiciaire d\'expulsion des occupants',
                                inputs: {
                                    ownerComplaint: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Dpt de plainte par le propritaire',
                                        mandatory: true,
                                    },
                                    justiceProcedure: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Existence d\'une procdure judiciaire',
                                        mandatory: true,
                                        condition({ ownerComplaint }) {
                                            return ownerComplaint === 1;
                                        },
                                    },
                                    justiceRendered: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Dcision de justice rendue',
                                        mandatory: true,
                                        condition({ ownerComplaint, justiceProcedure }) {
                                            return ownerComplaint === 1
                                                && justiceProcedure === 1;
                                        },
                                    },
                                    justiceRenderedBy: {
                                        type: 'text',
                                        label: 'Origine de la dcision',
                                        mandatory: true,
                                        condition({ ownerComplaint, justiceProcedure, justiceRendered }) {
                                            return ownerComplaint === 1
                                                && justiceProcedure === 1
                                                && justiceRendered === 1;
                                        },
                                    },
                                    justiceRenderedAt: {
                                        type: 'date',
                                        label: 'Date de la dcision',
                                        mandatory: true,
                                        condition({ ownerComplaint, justiceProcedure, justiceRendered }) {
                                            return ownerComplaint === 1
                                                && justiceProcedure === 1
                                                && justiceRendered === 1;
                                        },
                                    },
                                    justiceChallenged: {
                                        type: 'radio',
                                        options: yesNoValues,
                                        label: 'Contentieux relatif  la dcision de justice',
                                        mandatory: true,
                                        condition({ ownerComplaint, justiceProcedure, justiceRendered }) {
                                            return ownerComplaint === 1
                                                && justiceProcedure === 1
                                                && justiceRendered === 1;
                                        },
                                    },
                                    policeStatus: {
                                        type: 'radio',
                                        options: cfpValues,
                                        label: 'Concours de la force publique',
                                        mandatory: true,
                                    },
                                    policeRequestedAt: {
                                        type: 'date',
                                        label: 'Date de la demande du CFP',
                                        mandatory: true,
                                        condition({ policeStatus }) {
                                            return ['requested', 'granted'].indexOf(policeStatus) !== -1;
                                        },
                                    },
                                    policeGrantedAt: {
                                        type: 'date',
                                        label: 'Date d\'octroi du CFP',
                                        mandatory: true,
                                        condition({ policeStatus }) {
                                            return policeStatus === 'granted';
                                        },
                                    },
                                    bailiff: {
                                        type: 'text',
                                        label: 'Nom de l\'tude d\'huissiers',
                                        mandatory: false,
                                    },
                                },
                            }]
                            : [],

                    ],
                    wording: {
                        submit: 'Dclarer le site',
                        error: 'La dclaration du site a chou',
                        success: 'La dclaration du site a russi',
                    },
                    submit: (data) => {
                        const p = add(data);
                        p.then(({ plans }) => {
                            formDefinition.steps[1].sections[0].inputs.plans.options = plans.map(({ id, name, type }) => ({
                                label: name || type,
                                value: id,
                            }));
                        });
                        return p;
                    },
                },

                // {
                //     title: 'Dispositifs en cours sur le site',
                //     sections: [
                //         {
                //             title: 'Slection des dispositifs',
                //             description: 'Des dispositifs sont dj en place sur le territoire d\'implantation du site que vous venez de dclarer.',
                //             inputs: {
                //                 plans: {
                //                     type: 'checkbox',
                //                     label: 'Dispositifs',
                //                     description: 'Veuillez cocher, parmi ceux lists ci-dessous, les dispositifs dploys sur le site que vous venez de dclarer',
                //                     mandatory: true,
                //                     options: [],
                //                 },
                //             },
                //         },
                //     ],
                //     wording: {
                //         submit: 'Valider les dispositfs',
                //         error: 'L\'intgration du site aux dispositifs a chou',
                //         success: 'L\'intgration du site aux dispositifs a russi',
                //     },
                //     submit: (data, [{ town: { id: townId } }]) => Promise.all(data.plans.map((planId) => link(planId, townId))),
                // },
            ],
        };

        return {
            data: {},
            formDefinition,
        };
    },

    methods: {
        onStepComplete(index, stepData) {
            if (index === 0) {
                if (!stepData.plans || stepData.plans.length === 0) {
                    this.$nextTick(() => {
                        this.$refs.form.goToNextStep(null);
                    });
                }
            }
        },
        onComplete(stepData, [{ town: { id } }]) {
            this.$router.push(`/site/${id}`);
        },
    },
};
